@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Scheherazade+New:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Slabo+13px:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Gilroy-Medium:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Gilroy-SemiBold:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Gilroy-Regular:wght@400&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-slabo-13px: "Slabo 13px";
  --regular-14: Roboto;
  --font-scheherazade-new: "Scheherazade New";
  --font-gilroy-medium: Gilroy-Medium;
  --font-gilroy-semibold: Gilroy-SemiBold;
  --font-gilroy-regular: Gilroy-Regular;

  /* font sizes */
  --medium-36-size: 36px;
  --regular-14-size: 14px;
  --regular-16-size: 16px;
  --regular-12-size: 12px;
  --font-size-31xl: 50px;
  --regular-18-size: 18px;
  --font-size-19xl: 38px;
  --font-size-7xl: 26px;

  /* Colors */
  --colour-secondary-white-100-general: #fff;
  --color-darkslategray-100: #425f57;
  --color-darkslategray-200: #3c6255;
  --color-darkslategray-300: #1a3c1e;
  --light-gray: #d2d2d7;
  --color-darkolivegreen: #5f835f;
  --colour-main-grey-600: #5d6970;
  --color-darkseagreen-100: #a6bb8d;
  --color-darkseagreen-200: #88b087;
  --color-cadetblue: #61876e;
  --gray: #808080;
  --black: #121212;
  --black-brightness-90: rgba(18, 18, 18, 0.9);
  --color-black: #000;

  /* Gaps */
  --gap-9xs: 4px;
  --gap-5xs: 8px;
  --gap-base: 16px;
  --gap-xs: 12px;

  /* Paddings */
  --padding-13xl: 32px;
  --padding-5xl: 24px;
  --padding-base: 16px;
  --padding-sm: 14px;
  --padding-xs: 12px;

  /* border radiuses */
  --br-11xs: 2px;
}
