.rectangle {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.about,
.home {
  line-height: 24px;
}
.home {
  position: absolute;
  top: calc(50% - 12px);
  left: 0;
}
.about {
  left: 43.56%;
}
.about,
.contact,
.group {
  position: absolute;
  top: calc(50% - 12px);
}
.contact {
  left: 87.11%;
  line-height: 24px;
}
.group {
  width: 15.63%;
  right: 72.92%;
  left: 11.46%;
