.rectangleIcon {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  right: -24.7%;
  bottom: 0;
  left: 24.7%;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  opacity: 0.5;
  mix-blend-mode: normal;
}
.bg,
.buttonLabel {
  position: absolute;
  width: 100%;
  left: 0;
}
.bg {
  height: calc(100% + 0.19px);
  top: 0;
  right: 0;
  bottom: -0.19px;
  border-radius: var(--br-11xs);
  background-color: var(--color-darkslategray-200);
}
.buttonLabel {
  top: calc(50% - 11.91px);
  line-height: 24px;
  font-weight: 500;
  display: inline-block;
}
.elementbuttonmediumfilled {
  position: absolute;
  height: 3.41%;
  width: 7.53%;
  top: 64.31%;
  right: 42.89%;
  bottom: 32.28%;
  left: 49.58%;
  background-color: var(--color-darkslategray-200);
}
.uploadAPhoto {
  margin-block-start: 0;
  margin-block-end: 10px;
}
.confidenceGuaranteedMake {
  margin: 0;
}
.body,
.title {
  position: absolute;
  width: 100%;
  top: 72px;
  left: 0;
  line-height: 26px;
  display: inline-block;
}
.title {
  top: 0;
  font-size: var(--medium-36-size);
  line-height: 48px;
  font-weight: 500;
  color: var(--color-darkslategray-300);
}
.pricing,
.textBlocksectionlargetitle {
